@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,500&display=swap');


*{
    margin: 0;
    padding: 0;border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}


:root{
    --color-bg: #1f1f38; 
    --color-bg-varient:#2c2c6c ; 
    --color-primary: #4db5ff; 
    --color-primary-variant: rgba(77 , 181 , 225 , 0.4) ; 
    --color-white: #fff ; 
    --color-light: rgba(225 , 225 , 225 , 0.6 );
    
    
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-ms: 90%;
}

html{
    scroll-behavior: smooth;

}

::-webkit-scrollbar{
    display: none;

}

body{

    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    /* color: var(--color-white); */
    color: black;
    line-height: 1.7;
    /* background-image: url(./Asstes/07plain\ backgrounds\ dark\ blue\ checks\ web\ background\ free\ hd\ 245jpg.jpg); */
    /* background-color: white; */
    background-color: rgb(51, 0, 146);
}


/* ===================== Gerneral systels ================ */


.container{
    width: var(--container-width-lg);
    margin: 0 auto;

}

h1,
h1,
h2,
h3,
h4,
h5{
    font-weight: 2.5rem;

}


section{
    margin-top: 8rem;
    height: 100vh;

}


section > h2,section > h5 {
    text-align: center;
    color: var(--color-light);

}


section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;

}


.text_light{
    color: var(--color-light);

}


a{
    color: var(--color-primary);
    /* transition: ; */

}

a:hover{
    color: var(--color-white);

}


.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem ; 
    cursor: progress;
    border-radius: .4rem;
    border: 1pz solid var(--color-primary);
    /* transition: ; */
    /* background-color: red; */

}


.btn:hover{
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;

}


.btn_primary{
    background-color: var(--color-primary);
    color: var(--color-bg);

}


img{
    display: block;
    width: 100%;
    object-fit: cover;

}


/* ======================== MEdia --====================== */
@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;

    }

}
/* ======================= another media================= */
@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-ms);
    }
    section{
        margin-top: 2rem;

    }

}


/* ================== media  queery ================ */

@media screen and (max-width : 1024px ) {
    .header{
        height: 68vh;
    }
    
}

@media screen and (max-width : 600px ) {

    header{
        height: 100vh;

    }

    .header_social ,
    .scroll_down{
        display: none;
    }
    
}








