.services_container{
    display: grid; 
    grid-template-columns: repeat(3,1fr);
    gap: 3rem;
}

.service{
    background: var(--color-bg-varient);
    border-radius: 0 0 2rem 2rem ;
    border: 1px solid var((--color-primary));
    height: fit-content;
    transition: .3s ; 
}
.service:hover{
    background-color: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
}

.service_head{
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem ; 
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, .1); 
}
.service_head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service_list{
    padding: 3rem;
}

.service_list li{
    display: flex; 
    gap: 1rem;
    margin-bottom: 0.8rem;
}

.service_list-icon{
    color: var(--color-primary);
    margin-top: 2px;
}

.service_list p{
    font-size:  0.9rem;
}

@media screen and (max-width : 1024px ) {
    .services_container{
        grid-template-columns: repeat(1 , 1fr);
    }
    .service{
        height: auto; 
    }
    

    
}

@media screen and (max-width: 600px ) {

    .services_container{
        grid-template-columns: repeat(1 , 1fr);
        gap: 1.5rem;
    }
}