.container.textmonia_container {
    width: 40%;
}

.client_avatar {
    width: 4rem;
    aspect-ratio: 1/1;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto 3rem;
    border: .4rem solid var(--color-primary-variant);
}

.textmonia {
    background: rgb(42, 2, 135);
    text-align: center;
    border-radius: 2rem;
    /* background-color: red; */
    user-select: none;
}

.client_review{
    color: var(--color-light);
    /* background-color: red; */

    font-weight: 300;
    display: block;
    width: 80%;
    margin: .8rem auto 0 ;
}

@media screen and (max-width : 1024px ) {

    .container.textmonia_container{
        width: 60%;
    }
    
}

@media screen and (max-width: 600px) {
    .container.textmonia_container{
        width: var(--container-width-ms);

    }

    .client_review{
        width: var(--container-width-ms);
    }
    
}