nav{
    /* background : red; */
    background : rgba(0, 0, 0, .3);
    /* background-color: red; */
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem ;
    z-index: 2;
    position: fixed ;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: .8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: .9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    /* color: red; */
    font-size: 1.3rem;
    /* background-color: red; */
}

nav a:hover{
    background: rgba(0, 0, 0, .3);

}

nav a.active{
    background: var(--color-light);
     /* color: var(color); */
     color: white;

} 