header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height:100% ; 
    position: relative;

}


/* =================== CTA ============== */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* ====================== header social ================== */
.header_social{
    display: flex;
    flex-direction: column;
    gap: .8rem;
    position: absolute;
    left: 0;
    bottom: 2rem;

}


.header_social::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ================== img ================== */
.img{
    /* background : var(--color-primary); */
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    /* transform: translate(); */
    border-radius: 12rem 12rem 0 0 ;
    overflow: hidden;
}


/* ========================= scroll dwon================ */


.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    /* background-color: red; */
} 